@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* containers */
.container {
    max-width: 960px;
    height: auto;
}

.section {
    padding: 75px 0;  
}

.title {
    padding: 15px 0;
}

.background-layer {
    min-height: 65vh;
    margin: 25px;
}

.about {
    background: rgb(255, 224, 224);
}

.work {
    background: rgb(234, 255, 234)
}

.contact {
    background: rgb(225, 248, 255);
}

.uses {
    background: rgb(255, 243, 220);
}

.portfolio {
    background: rgb(236, 236, 236);
}
/* work */
.grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 12px;
}


/* uses */

.uses-section {
    padding: 15px 0;
}
/* footer styles */
footer {
    padding: 10px 0;
    text-align: center;
}

.link, .copyright{
    font-size: 0.9em;
}

.project {
    height: 200px;
    width: 100%;
    border: none;
    background: white;
    border-radius: 10px;
    display: block;
    text-align: left;
}

/* .project-img {
    width: 100%;
    height: 60%;
} */
li {
    margin-bottom: 1rem;
}
.title {
    font-size: 20px;
}
/* about */
.about-pitch, .bio {
    grid-column: 1 / span 2;
}
.fact-list {
    grid-column: 3 / span 2;
}

.quote {
    text-align: center;
    font-style: italic;
    padding: 30px;
    color:rgb(179, 179, 179);
}

.larger {
    font-size: 1.15em;
}

html {
    scroll-behavior: smooth;
}

